import { Directive, ElementRef , Input  , Output, EventEmitter  } from '@angular/core';
import { CdsTextarea } from '@cds/core/textarea';

@Directive({ selector: 'cds-textarea' })
export class CdsTextareaDirective {
  protected element: CdsTextarea;

  get status() {
    return this.element.status;
  }
  @Input() set status(value) {
    this.element.status = value;
  };
  get controlWidth() {
    return this.element.controlWidth;
  }
  @Input() set controlWidth(value) {
    this.element.controlWidth = value;
  };
  get validate() {
    return this.element.validate;
  }
  @Input() set validate(value: boolean | '') {
    this.element.validate = value === '' ? true : value;
  };
  get responsive() {
    return this.element.responsive;
  }
  @Input() set responsive(value: boolean | '') {
    this.element.responsive = value === '' ? true : value;
  };
  get layout() {
    return this.element.layout;
  }
  @Input() set layout(value) {
    this.element.layout = value;
  };

  @Output() layoutChange: EventEmitter<CustomEvent> = new EventEmitter();

  constructor(elementRef: ElementRef) {
    this.element = elementRef.nativeElement;
  }
}
